<template>
  <div class="hl-carousel">
    <div class="hl-carousel-list">
      <div v-for="(item, i) in carousellist" :key="i" class="carousel-item" :class="{active: activeIndex === i}">
        <div class="carousel-item-content area-wrapper">
          <div class="desc">
            <h2>{{item.title}}</h2>
            <div class="desc-text">
              <p>{{item.text1}}</p>
              <p v-if="item.text2">{{item.text2}}</p>
            </div>
            <div class="btn-wrapper">
               <div class="btn"><a :href="item.btnLink" target="_blank">{{item.btnText}}</a></div>
                <div v-if="i===0" class="btn-ppt"><a href="http://test.sfyf.cn/booking/frontlogin.html" target="_blank">视频演示</a></div>
            </div>
          </div>
          <div class="img-banner">
          </div>
        </div>
      </div>
    </div>
    <ul class="slick-dots">
      <li 
        v-for="(item, i) in carousellist"
        :key="i"
        class="dot"
        :class="{active: activeIndex === i}"
        @click="handleDotClick(i)"
      >
        <span></span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timer: null,
      activeIndex: 0,
      carousellist: [
         {
          title: "关于恒链",
          text1: "一站式物流SaaS服务提供商，提供在线订舱、舱单发送、智能客服、视频会议等服务，实现IPA智能业务流程自动化，全面提升员工工作效率，节省办公成本。",
          btnText: "免费试用",
          btnLink: "http://dev.sfyf.cn/admin/user/register",
        },
         {
          title: "报价查询",
          text1: "平台货代联盟运价查询，支持海量船公司和船代运价查询，显示30天内最低报价，为平台用户吸引大量客户。",
          btnText: "查看详情",
          btnLink: "http://test.sfyf.cn/booking/newEbooking.html"
        },
      ]
    };
  },
  mounted() {
    this.AutoBroadCast();
  },
  methods: {
    AutoBroadCast() {
      this.timer && clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.setNextActiveIndex();
      }, 8000);
    },
    handleDotClick(i) {
      this.activeIndex = i;
      this.AutoBroadCast();
    },
    setNextActiveIndex() {
      let index = this.activeIndex;
      if (index >= this.carousellist.length - 1) {
        this.activeIndex = 0;
      } else {
        ++this.activeIndex;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../style/mixin.scss";
@keyframes progress {
  0% {
    width: 5%;
  }
  100% {
    width: 100%;
  }
}
.hl-carousel {
  position: relative;
  .hl-carousel-list {
    position: relative;
    height: 540px;
    .carousel-item {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: none;
      background: #fff;
      &.active {
        display: block;
      }

      &:nth-child(1) {
        background: url("../../public/static/img/banner_bg_01.png") no-repeat;
        .img-banner {
          width: 518px;
          height: 420px;
          background: url("../../public/static/img/banner_01.png") no-repeat;
          background-size: 100% 100%;
        }
      }
      &:nth-child(2) {
        background: url("../../public/static/img/banner_bg_02.png") no-repeat;
        .img-banner {
          width: 494px;
          height: 420px;
          background: url("../../public/static/img/banner_02.png") no-repeat;
          background-size: 100% 100%;
        }
      }
    }
    .carousel-item-content {
      display: flex;
      height: 100%;
      justify-content: space-between;
      align-items: center;
      
      .desc {
        align-self: center;
        width: 440px;
        h2 {
          font-size: 36px;
          color: #ef7609;
          margin-bottom: 30px;
        }
        .desc-text {
          font-size: 18px;
          margin-bottom: 30px;
          line-height: 36px;
          color: #505050;
        }

        .btn {
          display: inline-block;
          width: 151px;
          height: 43px;
          background: url("../../public/static/img/btn_bg.png") no-repeat;
          background-size: 100% 100%;
          margin-right: 20px;
          cursor: pointer;
          font-size: 13px;
          letter-spacing: 2px;
          text-align: center;
          font-weight: 700;
          line-height: 43px;
          color: #fff;
          a {
            color: #fff;
          }
        }

        .btn-ppt {
          display: inline-block;
           width: 151px;
          height: 43px;
          background: url("../../public/static/img/btn_ppt.png") no-repeat;
          background-size: 100% 100%;
          cursor: pointer;
          font-size: 13px;
          letter-spacing: 2px;
          text-align: center;
          font-weight: 700;
          line-height: 43px;
          color: #fff;
          a {
            color: #fff;
          }
        }
      }
      .img {
        height: 300px;
        width: 260px;
        background: #ebb624;
      }
    }
  }
  .slick-dots {
    position: absolute;
    left: 50%;
    bottom: 30px;
    transform: translateX(-50%);
    z-index: 1;
    display: flex;
    .dot {
      width: 40px;
      height: 16px;
      margin-right: 10px;
      cursor: pointer;
      span {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        height: 3px;
        background: #f3dfa7;
        cursor: pointer;
        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          display: inline-block;
          height: 100%;
          width: 0;
          background: #ebb624;
        }
      }

      &.active {
        span {
          &::before {
            animation: progress 8s linear;
          }
        }
      }
    }
  }
}
</style>