<template>
  <div id="app">
    <template v-if="$isMobile">
      <MobileHeader class="header-custom" :class="{'header-style-scroll': isScroll}" ></MobileHeader>
      <router-view  :class="{'page-style-scroll': isScroll}"></router-view>
      <MobileFooter></MobileFooter>
      <div class="scroll-bar"  >
        <img src="../public/static/img/mobile/scroll_bar.png" alt="">
        <span class="btn to-up" @click.stop="scrollToTop" ></span>
        <span class="btn to-down" @click.stop="scrollToBottom" ></span>
      </div>
    </template>
     <template v-else>
      <Header class="header-custom" :class="{'header-style-scroll': isScroll}"></Header>
      <router-view :class="{'page-style-scroll': isScroll}"></router-view>
      <div class="divider-line area-wrapper"></div>
      <Footer></Footer>
    </template>
  </div>
</template>

<script>
import Header from "./components/header";
import Carousel from "./components/Carousel";
import Footer from "./components/Footer";
import MobileHeader from "./components//MobileHeader";
import MobileFooter from "./components/MobileFooter";

export default {
  name: "app",
  components: {
    Header,
    Carousel,
    Footer,
    MobileHeader,
    MobileFooter,
  },
  data() {
    return {
      isScroll: false
    };
  },
  created() {
    console.log(this.$router);
    console.log(this.$isMobile)
  },
  mounted() {
    this.listenScroll();
  },
  watch: {
    $route() {
      let app = document.querySelector("#app");
      app.scrollTop = 0;
    }
  },
  methods: {
    linkto(path) {
      let host = window.location;
      this.$router.push(path);
    },
    listenScroll() {
      let app = document.querySelector("#app");
      let header = document.querySelector(".header-custom");

      app.addEventListener("scroll", e => {
        let scrollTop = app.scrollTop;
        if (scrollTop >= 76) {
          this.isScroll = true;
        } else {
          this.isScroll = false;
        }
      }); 
    },
    scrollToTop() {
      let app = document.querySelector("#app");
      app.scrollTop = 0;
    },
    scrollToBottom() {
      let app = document.querySelector("#app");
      app.scrollTop = app.scrollHeight;
    },
  },
};
</script>

