  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .header-style-scroll {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 10;
    background: #fff;
    padding-top: 0;
    box-shadow: 0 2px 4px 0 rgba(133, 138, 180, 0.14);
    transition: all 0.6s;
  }
  .page-style-scroll {
    padding-top: 80px;
    // transition: all 1s;
  }
  .scroll-bar {
    position: fixed;
    top: 50%;
    transform: translateY(-505);
    right: 8px;
    z-index: 1000;
    visibility: visible;
    // &.show {
    //   visibility: visible;
    // }
    img {
      width: 32px;
    }
    .btn {
      position: absolute;
      left: 0;
      height: 50%;
      display: block;
      width: 100%;
      &.to-up {
        top: 0;
      }
      &.to-down {
        top: 50%;
      }
    }
  }
}
</style>
